<template>
  <div>
    <button
      type="button"
      @click="$router.go(-1)"
      class="btn btn-sm btn-style1 font-bold text-uppercase underline text-white"
      style="position: absolute; z-index: 10"
    >
      <span class="text-xl typcn typcn-arrow-left text-white"></span> Kembali
    </button>
    <svg
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 1440 320"
      class="animated fadeInDown"
    >
      <path
        fill="#0099ff"
        fill-opacity="1"
        d="M0,256L60,256C120,256,240,256,360,229.3C480,203,600,149,720,106.7C840,64,960,32,1080,69.3C1200,107,1320,213,1380,266.7L1440,320L1440,0L1380,0C1320,0,1200,0,1080,0C960,0,840,0,720,0C600,0,480,0,360,0C240,0,120,0,60,0L0,0Z"
      />
    </svg>
    <div class="card">
      <div class="card-header">Detail Ijin</div>
      <div class="card-body">
        <tr>
          <td>NIP</td>
          <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
          <td>{{ datanya.nip }}</td>
        </tr>
        <img
          :src="datanya.gambar"
          v-viewer
          class="kinoLightBox img-fluid rounded-circle float-right"
          style="margin-top: -10px; width: 40px; height: 40px"
        />
        Nama &nbsp;:&nbsp;
        {{ datanya.nama }}
        <br />
        Unit Kerja &nbsp;:&nbsp;
        {{ datanya.unit_kerja }}
        <tr>
          <td>Tanggal Ijin</td>
          <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
          <td>{{ format(datanya.tanggal_ijin) }}</td>
        </tr>
        <tr v-show="datanya.lama_keluar.indexOf('undefined') == -1">
          <td>Lama Keluar</td>
          <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
          <td>{{ datanya.lama_keluar }}</td>
        </tr>
        <!-- <tr>
          <td>Jenis Ijin</td>
          <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
          <td>{{datanya.jenis_pekerjaan}}</td>
        </tr> -->
        <tr>
          <td>Ijin</td>
          <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
          <td class="font-bold">{{ datanya.ijin }}</td>
        </tr>
        <tr>
          <td>Keperluan</td>
          <td class="pl-1 pr-1">&nbsp;:&nbsp;</td>
          <td>{{ datanya.keperluan }}</td>
        </tr>
        <span class="text-sm float-right"
          >{{ distanceToNow(datanya.createAt) }} Yang lalu</span
        >
        <br />
        <hr class="style13" />
        <div class="row">
          <div class="col-6 text-justify" v-if="datanya.disposisi_head">
            <p class="font-bold">Pimpinan</p>
            Nip : {{ datanya.nip2 }}<br />
            Nama : {{ datanya.nama2 }}<br />
            disposisi : {{ datanya.disposisi2 }}
          </div>
          <div class="col-6" v-if="!datanya.bukti">
            <p class="p-2">Upload gambar :</p>
            <input type="file" @input="upload" />
          </div>
          <div class="col-6" v-else>
            Photo Bukti :
            <img
              :src="datanya.bukti"
              class="img-fluid img-thumbnail"
              v-viewer
              alt=""
            />
          </div>
        </div>
        <br />
        <!-- <button type="button" @click="generateslink()" class="btn btn-sm btn-style9 "><span class="typcn typcn-printer"></span>Generate Link to print</button> -->
        <div v-if="vdata.link.length > 0" class="sm-form">
          <textarea
            type="text"
            id="link"
            name="link"
            rows="2"
            placeholder="link..."
            class="form-control md-textarea"
            v-model="vdata.link"
          ></textarea>
          <a :href="vdata.link"></a>
        </div>
        <button
          type="button"
          @click="print"
          class="btn btn-sm btn-style9 float-right"
        >
          <span class="typcn typcn-printer"></span> Print
        </button>
        <!-- <button
          type="button"
          v-if="datanya.posisi"
          @click="hapus"
          class="btn btn-sm btn-style9 float-left"
          style="background: red"
        >
          <span class="typcn typcn-device-tablet"></span> Hapus !
        </button> -->
        <button
          type="button"
          @click="balik"
          v-if="!datanya.posisi"
          class="btn btn-sm btn-style9"
          style="background: lightgreen"
        >
          <span class="typcn typcn-beaker"></span> Balik
        </button>
        <div class="text-center"></div>
      </div>
    </div>
    <!-- <div v-else>
      <p
        class="p-3 font-bold font-medium text-center animated bounce infinite"
      >DATA TIDAK DITEMUKAN ...</p>
    </div> -->
  </div>
</template>
<script>
import axios from "axios";
var id = require("date-fns/locale/id");
import firebase from "firebase";
let db = firebase.firestore();
import autophp from "@/plugins/autophp.js";
import imageCompression from "browser-image-compression";
let sdb = new autophp();
export default {
  data() {
    return {
      datanya: {},
      vdata: {
        disposisi1: "",
        link: "",
      },
      ready: false,
      units: [],
    };
  },
  methods: {
    async upload(event) {
       let that = this;
      let el = event.target.files[0];
       const options = {
         maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true
      }
      try {
        let file = el; // tambahkan data set kosong file:""
        let fd = new FormData();
        const compressedFile = await imageCompression(file, options);
        fd.append("file", compressedFile);
        fd.append('data', `select * from tbuser`)//database setting di server
        fd.append('secret', `dsdxxoi4#$(*#sdsaaada@#`)//database setting di server
        axios.post('https://upload.egov.balangankab.go.id/upload1.php', fd
       ).then(res => {
            that.$forceUpdate();
            that.$nuxt.$emit("busy", false);
            this.$forceUpdate();
                that.$nuxt.$emit("busy", false);
                sdb
                  .collection("ijin_keluar")
                  .doc()
                  .set({
                    id: that.$route.query.id,
                    bukti: "https://upload.egov.balangankab.go.id" + res.data,
                  })
                  .then((res) => {
                    console.log(res);
                    that.getData();
                  });
        })

      } catch (error) {
        console.log(error);
      }
    },
    balik() {
      let that = this;
      if (confirm("Apakah posisi sudah balik di kantor ?")) {
        sdb
          .collection("ijin_keluar")
          .doc()
          .set({
            id: this.$route.query.id,
            posisi: true,
            jam_balik: new Date(),
          })
          .then((res) => {
            console.log(res);
            that.getData();
          });
        // db.collection(`users/${this.$store.state.users.uid}/history-lembur`).doc(this.$route.query.id).set({
        //   posisi:true,
        //   jam_balik:new Date()
        // },{merge:true}).then(res=>{
        //   alert("Posisi berhasil di update ! ")
        //    this.$store.dispatch('notifikasi',{
        //           title:"Pegawai telah balik ke kantor!",
        //           body:""+that.datanya.from.nama+" Telah balik Ke kantor !",
        //           from:that.datanya.from,
        //           to:that.datanya.to,
        //         })
        // })
      }
    },
    hapus() {
      if (confirm("Apakah yakin ingin menghapus data Ijin Keluar ini ?")) {
        sdb
          .collection("ijin_keluar")
          .doc(this.$route.query.id)
          .delete()
          .then((res) => {
            console.log(res);
          });
      }
    },
    getData() {
      let that = this;
      sdb
        .collection("ijin_keluar")
        .doc()
        .select(
          `select *, NULL as password, NULL as password2,tb2.nama as nama, tb2.nip as nip,tb2.unit_kerja as unit_kerja,tb3.nip as nip2, tb3.nama as nama2, tb1.id as id from ijin_keluar tb1 left join tbuser tb2 on tb2.id=tb1.froms left join tbuser tb3 on tb3.id=tb1.tos where tb1.id='${this.$route.query.id}'`
        )
        .then((res) => {
          console.log(res);
          this.datanya = res[0];
          console.log("hadang");
          console.log("daranaya", res);
          setTimeout(() => {
            that.ready = true;
          }, 3000);
        });
    },
    generateslink() {
      this.vdata.link = `http://localhost:8082/lembur_generate?datanya=${JSON.stringify(
        this.datanya
      )}`;
      navigator.clipboard.writeText(this.vdata.link).then(
        function () {
          console.log("Async: Copying to clipboard was successful!");
        },
        function (err) {
          console.error("Async: Could not copy text: ", err);
        }
      );
    },
    print() {
      this.$router.push(`/lembur/history/laporan?id=${this.$route.query.id}`);
    },
    batal() {
      let that = this;
      if (confirm("yakin membatalkan Ijin Keluar?")) {
        let text = prompt("Alasan dibatalkan ? ", "-");
        console.log(that.datanya.from);
        this.$store.dispatch("notifikasi", {
          title: "Permintaan Ijin Keluar dibatalkan",
          body: text,
          link: "/lembur",
          from: this.$store.state.users,
          uid: that.datanya.from.uid,
          to: that.datanya.from,
        });
        db.collection("users")
          .doc(this.datanya.from.uid)
          .collection("lembur")
          .doc(this.$route.query.id)
          .delete()
          .then((res) => {
            that.$router.push("/lembur");
          });
      }
    },
    distanceToNow(date) {
      return this.$datefns.distanceInWordsToNow(new Date(date), {
        locale: id,
        includeSeconds: true,
      });
      //=> "3 days ago"
    },
    format(date) {
      return this.$datefns.format(new Date(date), "DD MMMM YYYY", {
        locale: id,
      });
    },
  },
  mounted() {
    this.getData();
    // db.collection(`users/${this.$route.query.uid}/history-lembur`).doc(this.$route.query.id).onSnapshot(res=>{
    //     let data = {
    //         id:res.id,
    //         ...res.data()
    //     }
    //     this.datanya = data
    //     console.log('hadang')
    //     console.log('daranaya',res.data())
    //     setTimeout(() => {
    //       that.ready=true
    //     }, 3000);
    // })
  },
  layout: "lembur",
};
</script>